.re-tooltip-wrapper {
  background: rgba(255, 255, 255, 0.85);
  padding: 0.5rem;
  border: 2px solid black;
}

.re-tooltip-wrapper > table > tbody > tr > th,
.re-tooltip-wrapper > table > tbody > tr > td {
  line-height: 2;
}

.re-tooltip-wrapper > table > tbody > tr.align-baseline > td {
  vertical-align: baseline;
}

.re-tooltip-col-first {
  padding-left: 1rem;
}

.re-tooltip-title {
  padding: 0 0.5rem;
  border-right: 2px solid black;
}

.re-tooltip-title h3 {
  font-size: 1.75em;
}

.re-tooltip-title h3 > small {
  margin-left: 1rem;
  font-size: 1rem;
  /*color: black;*/
}

.re-tooltip-bar {
  position: relative;
  width: 100%;
  height: 6px;
  background: white;
  border: 1px solid black;
}

.re-tooltip-fill {
  position: absolute;
  height: 100%;
  top: 0;
  left: 0;
}

.re-tooltip-label {
  font-size: 1em;
}

.re-tooltip-value {
  font-size: 1.25em;
  font-weight: bold;
}

.re-tooltip-unit {
  margin-left: 0.5rem;
  font-weight: normal;
  font-size: 0.5em;
  color: black;
}

.re-tooltip-man {
  height: 1.5rem;
  display: inline-block;
  margin: 0 0.25rem;
}

.re-tooltip-population svg {
  height: 100%;
}

.re-tooltip-population svg * {
  stroke: none;
}



